#user-top-wrapper {
    padding: 40px 0 45px;

    display: flex;
    justify-content: center;
}

.user-top-container {
    width: 1200px;
    min-width: 1200px;

    display: flex;
    align-items: center;
    gap: 25px;
}

.user-top-my-profile-image-box {
    border-radius: 50%;
    width: 120px;
    height: 120px;

    display: flex;
    justify-content: center;
    align-items: center;

    background-color: rgba(217, 217, 217, 1);   
    cursor: pointer;
}


.user-top-profile-image {
    height: 100%;
    width: 100%;
    border-radius: 50%;

    background-position: 50% 50%;
    background-size: 100% 100%;
}