.divider {
    width: 100%;
    border-top: 1px solid rgba(0,0,0,0.2);
}

.emphasis {
    font-weight: 700;
}

.black-button {
    border-radius: 46px;
    width: 95px;
    height: 30px;

    display: flex;
    align-items: center;
    justify-content: center;

    background-color: black;
    color: white;

    font-size: 14px;
    font-weight: 500;
    line-height: 140%;

    cursor: pointer;
}

.black-button:hover {
    background-color: rgba(0, 0, 0, 0.5);
}

.disable-button {
    border-radius: 46px;
    width: 95px;
    height: 30px;

    display: flex;
    align-items: center;
    justify-content: center;

    background-color: rgba(0,0,0,0.4);
    color: white;

    font-size: 14px;
    font-weight: 500;
    line-height: 140%;

    cursor: not-allowed;
}

.black-button:hover {
    background-color: rgba(0, 0, 0, 0.5);
}


.black-large-full-button {
    border-radius: 46px;

    width: 100%;
    height: 48px;

    display: flex;
    justify-content: center;
    align-items: center;

    background-color: black;
    color: white;

    font-size: 14px;
    font-weight: 500;
    line-height: 140%;

    cursor: pointer;
}

.black-large-full-button:hover {
    background-color: rgba(0, 0, 0, 0.6);
}

.white-button {
    border: 1px solid rgba(0,0,0,0.5);
    border-radius: 46px;
    width: 93px;
    height: 28px;

    display: flex;
    align-items: center;
    justify-content: center;

    background-color: white;
    color: rgba(0,0,0,0.5);

    font-size: 14px;
    font-weight: 500;
    line-height: 140%;

    cursor: pointer;
}

.white-button:hover {
    background-color: rgba(0, 0, 0, 0.1);
}

.icon-box {
    width: 32px;
    height: 32px;

    display: flex;
    align-items: center;
    justify-content: center;
}

.icon-box-small {
    width: 16px;
    height: 16px;

    display: flex;
    align-items: center;
    justify-content: center;
}

.icon-box-large {
    width: 40px;
    height: 40px;

    display: flex;
    align-items: center;
    justify-content: center;
}

.icon-button {
    width: 24px;
    height: 24px;

    background-position: center;
    background-size: cover;

    display: flex;
    align-items: center;
    justify-content: center;

    cursor: pointer;
}

.icon-button:hover {
    background-color: rgba(0, 0, 0, 0.5);
}

.icon{
    width: 100%;
    height: 100%;

    background-position: center;
    background-size: cover;
}

