#board-update-wrapper {
    border-top: 1px solid rgba(0,0,0,0.2);

    display: flex;
    justify-content: center;

    background-color: rgba(0,0,0,0.05);

}

.board-update-container {
    padding: 100px 24px;
    width: 996px;
    min-height: 1052px;

    background-color: white;
}

.board-update-box {
    display: flex;
    flex-direction: column;
    gap: 40px;
}

.board-update-title-box {
    width: 100%;
}

.board-update-title-input {
    width: 100%;

    border: none;
    outline: none;
    background: none;

    color: black;

    font-size: 32px;
    font-weight: 500;
    line-height: 140%;
}

.board-update-content-box {
    width: 100%;

    display: flex;
    gap: 16px;
}

.board-update-content-textarea {
    flex: 1;

    border: none;
    outline: none;
    resize: none;
    background: none;

    color: rgba(0,0,0,0.7);
    font-size: 16px;
    font-weight: 400;
    line-height: 160%;
}

.board-update-images-box {
    width: 100%;

    display: flex;
    flex-direction: column;
    gap: 20px;
}

.board-update-image-box {
    width: 100%;
    position: relative;
}

.board-update-image {
    width: 100%;
}

.image-close {
    position: absolute;
    top: 2px;
    right: 2px;
}